@import url("https://fonts.googleapis.com/css?family=Ubuntu");
@import url("https://fonts.googleapis.com/css?family=Nunito");
html {
  font-family: 'Nunito', sans-serif;
  box-sizing: border-box;
  background-color: #60b4f4;
  -webkit-font-smoothing: antialiased;
  height: 100%; }

*, *::before, *::after {
  box-sizing: inherit; }

body {
  margin: 0px;
  padding: 0px;
  height: 100%;
  margin-top: 70px; }

.topNav {
  box-shadow: 0px 5px 5px rgba(0, 0, 0, 0.1);
  background-image: linear-gradient(to right, #1985c1, #136694);
  height: 70px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0; }
  .topNav a {
    width: 100%;
    height: 100%;
    text-decoration: none;
    color: inherit;
    margin: 0px;
    padding: 0px; }
  .topNav .right {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    float: right; }
  .topNav .left {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    float: left; }
  .topNav .logo {
    display: flex;
    position: relative;
    justify-content: center;
    align-items: center;
    height: 100%;
    color: #97e6ff;
    font-weight: bold;
    font-size: 20px;
    padding: 10px;
    cursor: pointer;
    text-align: center;
    transition: all .2s ease-out; }
  .topNav .logo:hover {
    font-size: 25px;
    color: white; }
  .topNav .navItem {
    display: flex;
    position: relative;
    justify-content: center;
    align-items: center;
    background-color: #97e6ff;
    height: 100%;
    padding: 10px;
    cursor: pointer;
    text-align: center;
    transition: background-color .2s ease-out; }
    .topNav .navItem .hoverSelection {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      border-bottom: 0px solid #60b4f4;
      transition: border-bottom .2s ease-out; }
  .topNav .navItem:hover .hoverSelection {
    border-bottom: 5px solid #1985c1; }
  .topNav .navItem:active {
    background-color: #007397; }

.toastContainer {
  position: fixed;
  bottom: 20px;
  left: 100px; }
  .toastContainer .toast {
    margin: 5px;
    min-width: 300px;
    padding: 20px;
    text-align: left;
    background-color: #262626;
    color: white;
    box-shadow: 5px 5px 1px rgba(0, 0, 0, 0.1);
    opacity: 0;
    animation: riseUp .5s ease-out .8s forwards, disDown .5s ease-in 5s forwards; }

@keyframes riseUp {
  0% {
    opacity: 0;
    transform: translateY(50%); }
  100% {
    opacity: 1;
    transform: translateY(0); } }

@keyframes disDown {
  0% {
    opacity: 1;
    transform: translateY(0); }
  100% {
    opacity: 0;
    transform: translateY(100%); } }

.indexWrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 100%; }

.mainPageWrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-around; }

.pollPreview {
  margin: 20px;
  padding: 10px;
  background-color: #1985c1;
  box-shadow: 5px 5px 1px rgba(0, 0, 0, 0.1); }

.pollTable {
  border-collapse: collapse;
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.3); }
  .pollTable th {
    background-color: #60b4f4; }
  .pollTable th, .pollTable td {
    text-align: center;
    width: 200px;
    padding: 8px; }
  .pollTable tr:nth-child(even) {
    background-color: #64daff; }
  .pollTable tr {
    cursor: pointer; }
    .pollTable tr canvas {
      transition: all .3s ease-out; }
  .pollTable tr:hover {
    background-color: #00c0fd; }
    .pollTable tr:hover canvas {
      transform: scale(1.2); }

.partialWrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 100%; }

.footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 50px; }

.footerImg {
  filter: contrast(40%);
  height: 3rem; }

.indexRibbon {
  background-color: #97e6ff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 40px;
  text-align: center;
  height: 300px;
  width: 100%;
  text-shadow: 0 0 10px #3c3838; }

.pollRow {
  background-color: #97e6ff; }

.chart {
  width: 100%; }
  .chart div {
    background-color: steelblue;
    text-align: right;
    font-size: 1rem;
    padding: 10px;
    margin: 1px;
    color: white; }
    .chart div:hover {
      background-color: #294d6b; }

.bar {
  transition: all .3s ease-out;
  text-shadow: 0px 0px 5px black; }

.toastEmoji {
  float: right; }
